import React from 'react';
import '../styles/about.css';
import { useTranslation } from 'react-i18next';

const About = () => {

	const {t} = useTranslation();

    return (
        <div className="about" id="about">
			<div className="container">
				<div className="about__inner">
					<div className="about__text">
						<h2 className="about__spec">{t('about__spec 1')}</h2>
						<ul className="about__list">
							<li className="about__item">
								{t('about__item 1')}
							</li>
							<li className="about__item">
								{t('about__item 2')}
							</li>
							<li className="about__item">
								{t('about__item 3')}
							</li>
							<li className="about__item">
								{t('about__item 4')}
							</li>
							<li className="about__item">
								{t('about__item 5')}
							</li>
							<li className="about__item">
								{t('about__item 6')}
							</li>
							<li className="about__item">
								{t('about__item 7')}
							</li>
						</ul>
						<h2 className="about__spec">{t('about__spec 2')}</h2>
						<ul className="about__list">
							<li className="about__item">
								{t('about__item 2-1')}
							</li>
							<li className="about__item">
								{t('about__item 2-2')}
							</li>
							<li className="about__item">
								{t('about__item 2-3')}
							</li>
							<li className="about__item">
								{t('about__item 2-4')}
							</li>
							<li className="about__item">
								{t('about__item 2-5')}
							</li>
							<li className="about__item">
								{t('about__item 2-6')}
							</li>
						</ul>
						<h2 className="about__spec">{t('about__spec 3')}</h2>
						<ul className="about__list">
							<li className="about__item">
								{t('about__item 3-1')}
							</li>
						</ul>
					</div>
					<a className="main__btn" href="#work">{t('work title')}</a>
				</div>
				<hr/>
			</div>
		</div>
    );
};

export default About;